.mainHeader {
    display: flex;
    justify-content: left;
    align-items: center;
    text-align: center;

    position: fixed;
    width: inherit;
    height: 60px;
    top: 0;
    background-color: white;
    z-index: 10;
}
.mainHeader.recommend {
    background-color: rgba(0, 0, 0, 0);
}

.mainLogo {
    height: 40%;
    width: auto;
    object-fit: contain;

    padding-left: 30px;
    padding-right: 30px;
    padding-top: 10px;
    padding-bottom: 10px;

    cursor: pointer;
}