.viewPage{
  display: flex;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.06);
}

.layout {
  margin: 0;
  display: flex;
  width: 528px;
  min-width: 320px;
  min-height: 100vh;
  background-color: white;
  font-family: "SpoqaMedium";
  -webkit-tap-highlight-color : rgba(0, 0, 0, 0);
}

@media (max-width: 528px){
  .layout {
      width: 100%;
  }
}
@media (orientation: portrait) and (max-width: 768px){
  .layout {
      width: 100%;
  }
}
::-webkit-scrollbar {
  display: none;
}
@font-face {
  font-family: "SpoqaBold";
  src: url("./fonts/SpoqaHanSansNeo-Bold.ttf");
}
@font-face {
  font-family: "SpoqaLight";
  src: url("./fonts/SpoqaHanSansNeo-Light.ttf");
}
@font-face {
  font-family: "SpoqaMedium";
  src: url("./fonts/SpoqaHanSansNeo-Medium.ttf");
}
@font-face {
  font-family: "SpoqaRegular";
  src: url("./fonts/SpoqaHanSansNeo-Regular.ttf");
}
@font-face {
  font-family: "SpoqaThin";
  src: url("./fonts/SpoqaHanSansNeo-Thin.ttf");
}